function ClsHome() {
  fetch(
    "https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-D0047-091?Authorization=CWB-B4386A15-EEFA-46BA-8FF3-58FD4A639326&format=JSON&locationName=%E8%87%BA%E5%8C%97%E5%B8%82&elementName=MinT,MaxT,Wx"
  )
    .then((res) => res.json())
    .then(function (weatherAPI) {
      console.log(weatherAPI);
      let taipei = weatherAPI.records.locations[0].location[0];
      let weatherWx =taipei.weatherElement[0].time[0].elementValue[0].value;
      let weatherMinT = taipei.weatherElement[1].time[0].elementValue[0].value;
      let weatherMaxT = taipei.weatherElement[2].time[0].elementValue[0].value;
      // console.log(taipei.weatherElement[0].time[0].startTime.split(" ")[0].substring(5))
      document.querySelector(".weatherWx_today").innerHTML = weatherWx;
      document.querySelector(".weatherMinTMaxT_today").innerHTML = `${weatherMinT} ° / ${weatherMaxT} °`;
      // let weatherDate = taipei.weatherElement[0].time;
      // console.log("weatherWx = ",taipei.weatherElement[0].time[0].elementValue[1].value);
      let weatherToday= taipei.weatherElement[0].time[0].elementValue[1].value;
      // console.log(weatherToday);

      if(weatherToday === "01" || weatherToday === "02"){
        // console.log(document.querySelector("#weather_img"));
        document.querySelector("#weather_img").src="./img/home/sun.svg"
      }else if(weatherToday==="03"||weatherToday === "04" || weatherToday === "05" || weatherToday === "06" || weatherToday === "07"){
        document.querySelector("#weather_img").src="./img/home/cloud.svg"
      }else{
        document.querySelector("#weather_img").src="./img/home/rain.svg"
      }
      

      for(let i = 2; i < taipei.weatherElement[0].time.length ; i++){
        if( i%2 === 0)
      document.querySelector(".weather_date").innerHTML+= `<div>${taipei.weatherElement[0].time[i].startTime.split(" ")[0].substring(5)}</div>`;
      }

      // console.log(taipei.weatherElement[0].time[4].startTime.split(" ")[0].substring(5));
      for(let i = 2; i < taipei.weatherElement[0].time.length ; i++){
       taipei.weatherElement.map( (e)=>{
         if( i%2 === 0)
        document.querySelector(".weather_week").innerHTML += `<div">${e.time[i].elementValue[0].value} </div>`;
       })
     }
     
    });
    


    // 倒數計時
    function getTimeRemaining(endtime) {
      const total = Date.parse(endtime) - Date.parse(new Date()); 
      const seconds = Math.floor((total / 1000) % 60); 
      const minutes = Math.floor((total / 1000 / 60) % 60); 
      const hours = Math.floor((total / (1000 * 60 * 60)) % 24); 
      const days = Math.floor(total / (1000 * 60 * 60 * 24)); 
      return { 
          total, days, hours, minutes, seconds 
          }; 
    } 
    function initializeClock(id, endtime) { 
        const clock = document.getElementById(id); 
        const daysSpan = clock.querySelector('.days'); 
        const hoursSpan = clock.querySelector('.hours'); 
        const minutesSpan = clock.querySelector('.minutes'); 
        const secondsSpan = clock.querySelector('.seconds'); 
        function updateClock() { 
          const t = getTimeRemaining(endtime); 
          daysSpan.innerHTML = t.days; 
          hoursSpan.innerHTML = ('0' + t.hours).slice(-2); 
          minutesSpan.innerHTML = ('0' + t.minutes).slice(-2); 
          secondsSpan.innerHTML = ('0' + t.seconds).slice(-2); 
          if (t.total <= 0) { 
              clearInterval(timeinterval); 
              } 
       } 
       updateClock(); 
       const timeinterval = setInterval(updateClock, 1000); 
    } 
       const deadline = new Date(Date.parse(new Date()) + 3 * 24 * 60 * 60 * 1000); 
       initializeClock('clockdiv', deadline); 
}


export default ClsHome;