create_active_btn_pre.addEventListener("click",(e)=>{
    e.preventDefault();
    create_active_basicInfo[0].setAttribute("style","display:block");
    create_active_basicInfo[1].setAttribute("style","display:none");
    window.scroll(0, 0)
})

